const apiKey = "tAhsJVyOJe3QUmlDti8ozdEKqtJ64P5cFQaUVhiqaTXs5_27Re1G7NAUxODGzNRR-GWn3dTsKL-DfOF5MVKw1x1twKkz50PkRT05wWwX89K1wvXti92x67ImDCZaYHYx";

const Yelp = {
    search(term, location, sortBy) {
        return fetch(`https://cors-anywhere-enkhamar.herokuapp.com/https://api.yelp.com/v3/businesses/search?term=${term}&location=${location}&sort_by=${sortBy}`, {
            headers: {
                Authorization: `Bearer ${apiKey}`
            }
        }).then((response) => {
            return response.json();
        }).then((jsonResponse) => {
            if (jsonResponse.businesses) {
                return jsonResponse.businesses.map((business) => {
                    console.log(business);
                    return {
                        id: business.id,
                        imageSrc: business.image_url,
                        name: business.name,
                        address: business.location.address1,
                        city: business.location.city,
                        state: business.location.state,
                        zipCode: business.location.zip_code,
                        category: business.categories[0].title,
                        rating: business.rating,
                        reviewCount: business.review_count
                    };
                })
            }
        })
    }
};

export default Yelp;